let Mock = require('mockjs');
var Random = Mock.Random;

Mock.mock(
    /\/api\/orderDetail\?*/, {
        "success": true,
        "code": 200,
        "data": {
            "orderId": Random.id(),
            "name": Random.cname(),
            "phone": "1888888888",
            "address": Random.region(),
            "createTime": Random.date(),
            "orderNumber": Random.id(),
            "price": 20,
            "product": "一个萝卜"
        }
    }
).mock(
    /\/api\/confirm/, {
        "success": true,
        "code": 200,
        "data": {
            "message": "确认成功"
        }
    }
)

export default Mock


