import materialize from "materialize"
import { Observable } from 'rxjs'
import axios from "axios"
import "./server/mock"

const search = document.querySelector("#search");
const orderNumber = document.querySelector("#order-number");
let container = document.querySelector("#order-detail");
let button$ = Observable.fromEvent(search, "click");

function clear() {
    let label = document.querySelector("[for=order-number]");
    orderNumber.value = "";
    orderNumber.classList.remove("valid");
    label.classList.remove("active");
    container.innerHTML = "暂无订单信息...";
}

function bindTpl(data) {
    let tpl = Object.keys(data.data).reduce((tpl, key) => {
        return tpl += `<div class="row"><div class="col s4">${key}：</div><div class="col s6">${data.data[key]}</div></div>`;
    }, '')
    tpl += '<button class="btn waves-effect waves-light red lighten-3" type="submit" id="submit" name="action" alt="">自动确认订单</button>';
    container.innerHTML = tpl;
}

//请求参数
let search$ = button$
    .debounceTime(500)
    .mapTo(orderNumber)
    .map(x => x.value)
    .filter(x => x !== "");

//请求
let request$ = search$
    .switchMap((x) => {
        return Observable.fromPromise(axios.get("/api/orderDetail", { orderNumber: x }))
    });

//显示订单详情
let render$ = request$
    .map(res => res.data)
    .do(data => {
        bindTpl(data);
    })

//定时器
let interval$ = Observable.interval(1000).retry().take(5).startWith(0).takeUntil(search$);

//确认操作
let confirm$ = render$
    .concatMapTo(interval$)
    .map((i) => { return 3 - i })
    .do((x) => {
        let submit = document.querySelector("#submit");
        if (x > 0) {
            submit.innerHTML = `${x}秒后自动确认..`;
            submit.classList.add("disabled");
        } else {
            submit.innerHTML = "自动确认中..";
        }
    })
    .filter(function (i) { return i === -1 })
    .flatMap(function () {
        return Observable.fromPromise(axios.post("/api/confirm", { orderNumber: orderNumber.value }));
    }).do(data => {
        if (data.data.success) {
            alert("确认成功");
            clear();
        } else {
            alert("确认失败");
        }
    });
    
confirm$.subscribe()


